<template>
  <el-container style="height: 100%;">
    <el-aside style="width: 500px; overflow: auto;">
      <el-container>
        <el-header style="padding: 0px 0px 10px 0px; text-align: right; height: auto;">
          <DsfButton size="small" icon="sousuofangda" @click="openSysButtonSelect"></DsfButton>
          <DsfButton size="small" icon="zidingyi" @click="addCustomButton"></DsfButton>
          <DsfButton size="small" icon="Magnifier" type="auxiliary" @click="openIcon"></DsfButton>
        </el-header>
        <el-main style="padding: 0px; height: 100%;">
          <el-table
            border
            :show-header="true"
            :data="tableData"
            :highlight-current-row="true"
            @row-click="rowClickHandler"
          >
            <el-table-column label="#" type="index" align="center"></el-table-column>
            <el-table-column label="" prop="text">
              <template v-slot:default="scope">{{ scope.row.text || scope.row.title }}</template>
            </el-table-column>
            <el-table-column label="" prop="caption"></el-table-column>
            <el-table-column label="" prop="text">
              <template v-slot:default="scope">
                <DsfButton btn-style="icon-text" size="small" :icon="getIcon(scope.row.icon)"></DsfButton>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template v-slot:default="scope">
                <DsfButton
                  btn-style="icon-text"
                  icon="shanchu"
                  size="small"
                  title=""
                  @click="removeButton(scope.row)"
                ></DsfButton>
                <DsfButton
                  btn-style="icon-text"
                  icon="shang"
                  size="small"
                  title=""
                  @click="up(scope.row)"
                ></DsfButton>
                <DsfButton
                  btn-style="icon-text"
                  icon="xia"
                  size="small"
                  title=""
                  @click="down(scope.row)"
                ></DsfButton>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-aside>
    <el-main style="padding: 0px;">
      <el-container>
        <el-main>
          <el-form :model="formModel" label-width="120px">
            <el-form-item label="">
              <el-input v-model="formModel.caption" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.alias" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.text" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.title" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.icon" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="">
              <span>{{ formModel.isSystem ? "" : "" }}</span>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.actionName" :disabled="!allowEdit() || formModel.isSystem"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="formModel.btnStyle" :disabled="!allowEdit()">
                <el-option value="" label=""></el-option>
                <el-option value="icon-text" label=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="formModel.type" :disabled="!allowEdit()">
                <el-option value="" label=""></el-option>
                <el-option value="auxiliary" label=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="formModel.if" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c;"></span>
            </el-form-item>
            <el-form-item v-if="formModel.actionName === 'system_flow_send'" label="">
              <el-input v-model="formModel.flowSendOverEvent" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c;">
                
              </span>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="formModel.isMore" :disabled="!allowEdit()"></el-switch>
            </el-form-item>
            <el-form-item v-if="formModel.actionName === 'system_flow_send'" label="">
              <el-switch v-model="formModel.isLinkSend" :disabled="!allowEdit()"></el-switch>
            </el-form-item>
            <el-form-item v-if="isDblClickBind" label="">
              <el-switch v-model="formModel.isDblClick" :disabled="!allowEdit()" @change="dblClickChange"></el-switch>
            </el-form-item>
            <template v-for="(param, $index) in formModel.params">
              <el-form-item :key="$index" :label="'' + ($index + 1)">
                <el-input
                  v-model="param.value"
                  :placeholder="param.desc + ' :' + (param.default || '')"
                  :disabled="!allowEdit()"
                >
                  <!-- <DsfIcon slot="suffix" :name="js"></DsfIcon> -->
                  <i
                    slot="suffix"
                    class="iconfont icon-js"
                    style="cursor: pointer;"
                    title=""
                    @click="openEditor(param, !allowEdit())"
                  ></i>
                </el-input>
              </el-form-item>
            </template>
          </el-form>
        </el-main>
      </el-container>
      <el-dialog title="" :visible.sync="sysButtonSelect" width="500px" append-to-body>
        <template>
          <div style="height: 500px; overflow: auto;">
            <el-tree
              ref="tree"
              :data="treeData"
              :props="defaultProps"
              node-key="text"
              :default-checked-keys="[]"
              default-expand-all
              show-checkbox
            ></el-tree>
          </div>
        </template>
        <template v-slot:footer>
          <dsf-button @click="addSysButton"></dsf-button>
          <dsf-button type="auxiliary" @click="closeSysButtonSelect"></dsf-button>
        </template>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
let rfunction = /^\s*function(\s+\w*)?\(.*\)\s*{/g;
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import sysButton from "_platform/dsf/pc/sysButton";
import buttonParams from "_platform/dsf/designer/page/buttonParams.txt";
export default {
  name: "DesButtonEdit",
  mixins: [propertyDialogSetting],
  props: {
    defaultShowText: {
      type: Boolean,
      default: true,
    },
    defaultButtonStyle: {
      type: String,
      default: "",
    },
    isDblClickBind: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      tableData: [],
      formModel: {},
      sysButtonSelect: false,
      treeData: [],
      defaultProps: {
        label: "text",
        children: "buttons",
      },
    };
  },
  created() {
    let data = dsf.mix(true, [], this.currentData);
    this.tableData = data;
    this.treeData = sysButton.groups;
  },
  mounted() {},
  methods: {
    openSysButtonSelect() {
      if (this.$refs.tree) {
        this.$refs.tree.setCheckedKeys([]);
      }

      this.sysButtonSelect = true;
    },
    allowEdit() {
      if (dsf.isEmptyObject(this.formModel)) {
        return false;
      }
      return true;
    },
    addSysButton() {
      let nodes = this.$refs.tree.getCheckedNodes(true);
      let buttons = _.map(nodes, (node) => {
        let btn = this.createButton(node, true);
        if (!this.defaultShowText) {
          btn.text = "";
        }
        if (this.defaultButtonStyle) {
          btn.btnStyle = this.defaultButtonStyle;
        }
        if (!btn.icon) {
          btn.icon = "icon-shezhi";
        }
        if (!btn.title && btn.text) {
          btn.title = btn.text;
        }
        return btn;
      });
      this.tableData.push(...buttons);
      this.sysButtonSelect = false;
    },
    addCustomButton() {
      let button = this.createButton({
        caption: "",
        text: !this.defaultShowText ? "" : "",
        btnStyle: this.defaultButtonStyle ? this.defaultButtonStyle : "",
        icon: "icon-shezhi",
      });
      this.tableData.push(button);
    },
    closeSysButtonSelect() {
      this.sysButtonSelect = false;
    },
    yes() {
      return this.tableData;
    },
    rowClickHandler(row) {
      this.formModel = row;
    },
    createButton(obj, isSystem) {
      let c = {
        caption: "",
        text: "",
        alias: "",
        icon: "",
        btnStyle: "",
        title: "",
        isSystem: isSystem,
        actionName: "",
        if: "",
        flowSendOverEvent: "",
        type: "",
        isMore: false,
        isLinkSend: false,
        disabled: false,
        isDblClick: false,
        params: [],
      };
      let button = dsf.mix(true, {}, c, obj);
      return button;
    },
    openIcon() {
      window.open(dsf.url.getWebPath("$/pc/iconfont/demo_index.html"), "icondemo");
    },
    getIcon(icon) {
      let temp = "icon-";
      if (icon.startsWith(temp)) {
        return icon.substr(temp.length);
      }
      return icon;
    },
    removeButton(button) {
      dsf.array.remove(this.tableData, button);
    },
    up(button) {
      let index = _.findIndex(this.tableData, (b) => b == button);
      if (index > 0) {
        let targetButon = this.tableData[index - 1];
        this.$set(this.tableData, index - 1, button);
        this.$set(this.tableData, index, targetButon);
      }
    },
    down(button) {
      let index = _.findIndex(this.tableData, (b) => b == button);
      if (index > 0) {
        let targetButon = this.tableData[index + 1];
        this.$set(this.tableData, index + 1, button);
        this.$set(this.tableData, index, targetButon);
      }
    },
    dblClickChange(v) {
      if (v === true) {
        _.each(this.tableData, (row) => {
          if (this.formModel != row) {
            row.isDblClick = false;
          }
        });
      }
    },
    openEditor(param, disabled) {
      if (disabled) {
        return;
      } else {
        let v = param.value.trim();
        if (v) {
          if (!this.testCodeBlock(param)) {
            v = buttonParams.replace("$$0$$", "return " + JSON.stringify(v));
          }
        } else {
          v = buttonParams.replace("$$0$$", "return ''");
        }
        dsf.layer.pc.openDialog({
          title: "",
          fullscreen: true,
          overflow: "hidden",
          content: "MonacoEditor",
          params: {
            code: v,
            language: "javascript",
          },
          btns: [
            {
              text: "",
              handler: (component) => {
                param.value = component.getContent();
              },
            },
            {
              text: "",
            },
          ],
        });
      }
    },
    testCodeBlock(param) {
      let v = param.value.trim();
      if (v) {
        rfunction.lastIndex = 0;
        return rfunction.test(v);
      }
      return false;
    },
  },
};
</script>
